import React, { useContext, useRef, useReducer, useState, useEffect } from 'react'
import { TaskContext, ModalContext, DateTimeNowContext, ListVPRefContext } from './Contexts'
import TaskForm from './TaskForm'
import { DATETIME_OPTIONS, shown_dateTime } from './DateTimeUtils'
import Icon from './Icon'
import Draggable from 'react-draggable'

export default function ItemInList({children}) {
    // console.log('render item in list')

    const modal = useContext(ModalContext)
    const task = useContext(TaskContext)
    const [clicked, setClicked] = useState(false)

    // Dragging
    const nodeRef = useRef(null)
    const [bg_style, setBg_style] = useState(0)
    const [dragState, dispatchDragState] = useReducer(dragStateReducer, {x:0, y:0})
    const mainElement = useContext(ListVPRefContext)
    const [bounds, setBounds] = useState(DRAG_SETTINGS.BOUNDS)

    // Using Effects to show modal with modal.setContent, because we want to call setContent after a drag, but we can't directly give it to the drag event as a callback because the dragging happens in renders and React will give a warning about setting state in renders.
    // And we need to call it after a drag instead of setting it as an onClick, because the dragging replaces click events on mobile, so we have to detect a click by checking if the 'drag' began and ended at the same drag position
    useEffect(() => {
            dispatchDragState({
                type: 'link_refs',
                dispatchDragState,
                setBounds,
                mainElement
                })
        },
        // eslint-disable-next-line
        [])

    useEffect(
        () => {
            if (clicked) {
                modal.setContent(
                    <TaskForm oldTask={task} handleCloseForm={()=>{modal.setContent(null)}}/>
                )
                setClicked(false)
            }
        },
        // eslint-disable-next-line
        [clicked]
    )

    // Done animation
    const [itemOpacity, setItemOpacity] = useState(100)
    const [doneClasses, setDoneClasses] = useState(`use-color-transitions bg-secondary-subtle`)
    const TRANSITION_DURATION = 200
    const transition = `opacity ${TRANSITION_DURATION}ms ease-in-out 20ms`

    return (
        <div className={'overlap-container'} style={{transition, opacity: itemOpacity}} >
            <div className={'overlap fs-1 d-flex justify-content-center align-items-center bg-primary'}
            style={{width: DRAG_SETTINGS.BOUNDS.right, opacity: bg_style}} >
                <Icon i='bi-check-lg'/>
            </div>
            <Draggable
                axis='x'
                bounds={bounds}
                defaultClassNameDragging={'bg-dark-subtle'}
                onStart={(e, data)=>{
                    dispatchDragState({type: 'onStart', data: data})
                }}
                onDrag={(e, data)=>{
                    dispatchDragState({
                        type: 'onDrag',
                        data: data
                    })
                    setBg_style(data.x/DRAG_SETTINGS.BOUNDS.right)
                }}
                onStop={(e, data)=>{
                    dispatchDragState({
                        type: 'onStop',
                        data: data,
                        onClick: ()=>setClicked(true),
                        onDragSuccess: ()=>{
                            setItemOpacity(0)
                            setDoneClasses(`bg-primary`)
                            setTimeout(
                                ()=>{
                                    task.setDone()
                                    task.write_to_DB()
                                }, TRANSITION_DURATION*2
                            )
                        },
                    })
                    setBg_style(dragState.x/DRAG_SETTINGS.BOUNDS.right)
                }}
                nodeRef={nodeRef}
                position={dragState}
                >
                    <div ref={nodeRef} className={'overlap d-flex align-items-center ps-3 pe-2 pt-2 pb-2 ' + doneClasses}>
                        {children}
                    </div>
            </Draggable>
        </div>
    )
}

export const ListItemCheckbox = () => {
    const task = useContext(TaskContext)
    return (
        <div className='me-3'>
            { task.doneDate? <Icon i='bi-check-square-fill'/> : <Icon i='bi-square'/> }
        </div>
    )
}

export const ListItemName = () => {
    const task = useContext(TaskContext)
    return (
        <div className={ ` lh-sm fs-5 me-auto `+ decorate_by_priority(task.priority) }>
            {task.taskName}
        </div>
    )
}

export const ListItemDateTime = ({options=DATETIME_OPTIONS.FULL}) => {
    const task = useContext(TaskContext)
    const today_tomorrow = useContext(DateTimeNowContext)
    return (
        <div className='flex-shrink-0 align-self-start text-body-tertiary'>
            {shown_dateTime(task.dueDate, options, today_tomorrow)}
        </div>
    )
}

export const decorate_by_priority = (p) => {
    const high = 'text-info'
    const medium = ''
    const low = 'text-body-tertiary'
    if (p >= 6) {
        return high
    }
    else if (p >= 3) {
        return medium
    }
    else {
        return low
    }
}

const DRAG_SETTINGS = {
    BOUNDS: {left: 0, right: 125},
    SNAP: {left: 50, right: 75},
    AXIS_LOCK_THRESHOLD: 8,
    LOCKED_BOUNDS: {left: 0, right: 0},
    VIBE: {
        SET_DONE: [0, 50, 30],
        SET_NOT_DONE: [0, 50, 15]
    }
}

function dragStateReducer(state, action) {
    const data = action.data
    switch (action.type) {
        case 'link_refs': {
            state.dispatchDragState = action.dispatchDragState
            state.setBounds = action.setBounds
            state.mainElement = action.mainElement
            return state
        }
        case 'onStart': {
            console.log('%c drag start', "color: white; background-color: green; padding: 5px; width: 100%;")
            state.startX = data.x
            state.startY = data.y

            // whether the task should be set to done when the drag reaches onStop
            state.reached_end = false

            // axis lock
            state.lock = null

            // scroll inertia
            state.datapointsY = []

            return state
        }
        case 'onDrag': {
            // lock if y
            if (state.lock === null) {
                if (Math.abs(data.x) > DRAG_SETTINGS.AXIS_LOCK_THRESHOLD) {
                    state.lock = 'x'
                }
                else if (Math.abs(data.y) > DRAG_SETTINGS.AXIS_LOCK_THRESHOLD) {
                    state.lock = 'y'
                    state.setBounds(DRAG_SETTINGS.LOCKED_BOUNDS)
                }
            }
            else if (state.lock === 'y') {
                state.mainElement.current.scrollBy({
                    top: -(data.deltaY),
                    behavior: "instant",
                })
                // save data points for inertia calculation
                state.datapointsY.push(data.deltaY)
            }
            else { // x locked
                
            }
            
            if (!state.reached_end && data.x > DRAG_SETTINGS.SNAP.right) {
                // snap to right and set to done
                state.x = DRAG_SETTINGS.BOUNDS.right
                state.reached_end = true
                navigator.vibrate(DRAG_SETTINGS.VIBE.SET_DONE)
                state.cancel_click = true // cancel the possibility of interpreting this drag as a click
            }
            else if (state.reached_end && data.x < DRAG_SETTINGS.SNAP.left) {
                // snap to left and undo set to done
                state.x = DRAG_SETTINGS.BOUNDS.left
                state.reached_end = false
                navigator.vibrate(DRAG_SETTINGS.VIBE.SET_NOT_DONE)
            }

            return state
        }
        case 'onStop': {
            if (!state.cancel_click && data.x === state.startX && data.y === state.startY) {
                action.onClick()
            }
            else {
                // assumes state.thisDispatcher has been initialized with setThisDispatcher
                setTimeout(()=>{
                    state.dispatchDragState({type: 'undo_cancel_click'})
                    state.setBounds(DRAG_SETTINGS.BOUNDS)
                }, 100)
                if (state.reached_end) {
                action.onDragSuccess()
                }
                let SAMPLE_SIZE = 5
                if (state.datapointsY.length > SAMPLE_SIZE) { // do inertial scroll
                    let sample = state.datapointsY.slice(-SAMPLE_SIZE)
                    //console.log(state.datapointsY)
                    state.datapointsY = []
                    let inertia = sample.reduce(
                        (sum, currentValue) => sum + currentValue,
                        0
                    )
                    inertia = inertia/SAMPLE_SIZE
                    inertialScroll(state.mainElement.current, inertia)
                    console.log('%cinertial scroll by', "color: white; background-color: yellow; padding: 5px; width: 100%;")
                    console.log(inertia)
                }
            }
            console.log('%c drag stopped', "color: white; background-color: red; padding: 5px; width: 100%;")
            return state
        }
        case 'undo_cancel_click': {
            state.cancel_click = false
            return state
        }
        default: return state
    }
}

function inertialScroll(element, distance) {
    let timeStamp = Date.now()
    distance = distance * 2 // boost factor
    function scroll() {
        let currentTime = Date.now()
        let timeInterval = currentTime - timeStamp
        let delta = 0.001 * timeInterval

        if (distance < 0) {delta = -delta} // set delta to be in the direction of distance

        if (Math.abs(distance) > Math.abs(delta) // check that delta has larger magnitude than distance
        && Math.abs(distance) > 2) // ignore smaller values of distance for smoother animation
        {
            distance = distance - delta
            element.scrollBy({
                top: -(distance),
                behavior: "instant",
            })
            requestAnimationFrame(scroll)
        }
        else {
            console.log(`scroll finished`)
        }
    }
    requestAnimationFrame(scroll)
}